import React from 'react'
import { IconButton } from '@material-ui/core'
import AddIcon from '@material-ui/icons/FlightLand'

const EndFlightButton = props => {
  return (
    <IconButton
      onClick={props.onClick}
      size={props.small ? 'small' : 'medium'}
      color='primary'
    >
      <AddIcon fontSize={props.small ? 'default' : 'large'} />
    </IconButton>
  )
}

export default EndFlightButton