import React from 'react'
import { connect } from 'react-redux'

import addLeadingZero from '../../utils/addLeadingZero'

const FlightTime = props => {
  const rawSeconds = props._timeStamp < props.start ? 0 : (props._timeStamp - props.start) / 1000
  const flightHours = addLeadingZero(Math.floor(rawSeconds / 3600))
  const flightMinutes = addLeadingZero(Math.round(rawSeconds % 3600 / 60))
  return (
    <React.Fragment>
      {flightHours}:{flightMinutes}
    </React.Fragment>
  )
}

const mapStateToProps = state => ({
  _timeStamp: state.clock.timeStamp
})


export default connect(
  mapStateToProps,
)(FlightTime)