import React from 'react'

import { TextField, IconButton, Popper, } from '@material-ui/core'
import { MuiPickersUtilsProvider, KeyboardTimePicker as TimePicker, KeyboardDatePicker as DatePicker } from '@material-ui/pickers'
import { Modal, ModalBody, ModalFooter, Button } from 'reactstrap'
import { Autocomplete } from '@material-ui/lab';
import MomentUtils from '@date-io/moment';
import ClockIcon from '@material-ui/icons/AccessTime'
import DoneIcon from '@material-ui/icons/Done'
import DeleteIcon from '@material-ui/icons/DeleteOutline'

import addLeadingZero from '../../utils/addLeadingZero'
import tips from './tips'

const PopupPopper = props => <Popper {...props} style={{ maxWidth: 300, width: 'auto' }} />

class EditItem extends React.Component {
  constructor(props) {
    super()
    const d = props.data
    this.state = {
      inputs: {
        pilot: '' + d.pilot,
        instructor: '' + d.instructor,
        glider: '' + d.glider,
        task: '' + d.task,
        startType: '' + d.startType,
        operator: '' + d.operator
      },
      id: d.id,
      flightStart: new Date(d.startTime),
      flightEnd: new Date(d.endTime),
      towTime: new Date(d.endTow),
      callendar: new Date(d.startTime),
      errors: {},
      isError: false,
      isTimeError: false,
      modalDeleteConfirmation: false
    }
  }

  setTime = input => date => {
    if (this.state.isTimeError) {
      this.setState({ [input]: date && date._d }, this.checkTimes)
    } else {
      this.setState({ [input]: date && date._d })
    }
  }

  checkInputs = () => {
    const inputs = Object.keys(this.state.inputs).filter(el => {
      if (el === 'instructor') return false
      if (el === 'operator' && this.state.inputs.startType !== 'wyciągarka' && this.state.inputs.startType !== 'autohol') return false
      return true
    })
    const errors = {}
    inputs.forEach(el => {
      errors[el] = !this.state.inputs[el]
    })
    const isError = Object.values(errors).includes(true)

    this.setState({ errors, isError })

    return isError
  }

  checkTimes = () => {
    let isTimeError = false

    const year = this.state.callendar.getFullYear()
    const month = this.state.callendar.getMonth()
    const day = this.state.callendar.getDate()

    const fS = this.state.flightStart
    const fE = this.state.flightEnd
    const times = {
      startTime: new Date(year, month, day, fS.getHours(), fS.getMinutes(), fS.getSeconds()),
      endTime: new Date(year, month, day, fE.getHours(), fE.getMinutes(), fE.getSeconds()),
    }

    if (this.state.inputs.startType === 'samolot') {
      const tE = this.state.towTime
      Object.assign(times, { endTow: new Date(year, month, day, tE.getHours(), tE.getMinutes(), tE.getSeconds()) })
      if (times.startTime > times.endTow || times.endTow > times.endTime) isTimeError = true
    }

    if (times.startTime > times.endTime) isTimeError = true
    if (this.state.isTimeError !== isTimeError) this.setState({ isTimeError })

    return isTimeError || times
  }

  inputChange = (type) => (evt, newValue) => {
    const errors = this.state.errors
    if (this.state.errors.hasOwnProperty(type)) {
      if (newValue) errors[type] = false
    }
    if (type === 'startType') {
      if (newValue !== 'wyciągarka' && newValue !== 'autohol') errors.operator = false
    }
    const isError = Object.values(errors).includes(true)
    this.setState({
      inputs: {
        ...this.state.inputs,
        [type]: newValue,
      },
      errors,
      isError
    })
  }

  save = () => {
    const isInputError = this.checkInputs()
    const isTimeError = this.checkTimes()
    if (isInputError === true || isTimeError === true) {
      if (isTimeError === true) {
        let alertMsg = 'Nie można lądować przed startem. Popraw godziny.'
        if (this.state.inputs.startType === 'samolot') alertMsg = 'Nie można lądować przed startem. Czas lądowania samolotu powinien się zawierać pomiędzy startem a lądowaniem. Popraw godziny.'
        alert(alertMsg)
      }
      return
    }
    const flight = {
      id: this.state.id,
      startTime: isTimeError.startTime.getTime(),
      endTime: isTimeError.endTime.getTime(),
      ...this.state.inputs
    }
    if (flight.startType === 'samolot') {
      Object.assign(flight, { endTow: isTimeError.endTow.getTime() })
    }
    this.props.editFlight(flight)
  }

  deleteItem = () => {
    this.props.deleteFlight(this.state.id)
  }

  render() {
    let flightTime = (this.state.flightEnd - this.state.flightStart) / 1000
    if (flightTime < 0) flightTime = 0
    const flightHours = addLeadingZero(Math.floor(flightTime / 3600))
    const flightMinutes = addLeadingZero(Math.round(flightTime % 3600 / 60))


    return (
      <tr className='chronometer__table--add'>
        <MuiPickersUtilsProvider utils={MomentUtils}>
          <td >
            {this.state.id}
          </td>
          <td>
            <div>
              <DatePicker
                KeyboardButtonProps={{ size: 'small' }}
                style={{ width: 110 }}
                invalidDateMessage='Zły format'
                maxDateMessage='Tylko przeszłe daty'
                disableFuture
                value={this.state.callendar}
                format='DD/MM/YY'
                onChange={date => {
                  this.setState({ callendar: date._d })
                }}
                animateYearScrolling
              />
            </div>
          </td>
          <td>
            <div className='chronometer__table--add__date'>
              <TimePicker
                KeyboardButtonProps={{ size: 'small' }}
                keyboardIcon={<ClockIcon />}
                ampm={false}
                style={{ width: 110 }}
                invalidDateMessage='Zły format'
                error={this.state.isTimeError}
                openTo='hours'
                views={['hours', 'minutes', 'seconds']}
                format='HH:mm:ss'
                value={this.state.flightStart}
                onChange={this.setTime('flightStart')}
              />
            </div>
          </td>
          <td>
            <div className='chronometer__table--add__date'>
              <TimePicker
                KeyboardButtonProps={{ size: 'small' }}
                keyboardIcon={<ClockIcon />}
                ampm={false}
                style={{ width: 110 }}
                invalidDateMessage='Zły format'
                error={this.state.isTimeError}
                openTo='hours'
                views={['hours', 'minutes', 'seconds']}
                format='HH:mm:ss'
                value={this.state.flightEnd}
                onChange={this.setTime('flightEnd')}
              />
            </div>
          </td>
          <td>
            <Autocomplete
              autoHighlight
              PopperComponent={PopupPopper}
              disableClearable
              freeSolo
              options={tips.people.filter(el => el.toLowerCase().startsWith(this.state.inputs.pilot.toLowerCase()))}
              value={this.state.inputs.pilot}
              onChange={this.inputChange('pilot')}
              onInputChange={this.inputChange('pilot')}
              renderInput={params => (
                <TextField
                  {...params}
                  multiline
                  fullWidth
                  error={this.state.errors.pilot}
                />
              )}
            />
          </td>
          <td>
            <Autocomplete
              autoHighlight
              PopperComponent={PopupPopper}
              disableClearable
              freeSolo
              options={tips.people.filter(el => el.toLowerCase().startsWith(this.state.inputs.instructor.toLowerCase()))}
              value={this.state.inputs.instructor}
              onChange={this.inputChange('instructor')}
              onInputChange={this.inputChange('instructor')}
              renderInput={params => (
                <TextField
                  {...params}
                  multiline
                  fullWidth
                />
              )}
            />
          </td>
          <td>
            <Autocomplete
              autoHighlight
              PopperComponent={PopupPopper}
              disableClearable
              freeSolo
              options={tips.gliderOptions.filter(el => el.toLowerCase().startsWith(this.state.inputs.glider.toLowerCase()))}
              value={this.state.inputs.glider}
              onChange={this.inputChange('glider')}
              onInputChange={this.inputChange('glider')}
              renderInput={params => (<TextField
                {...params}
                multiline
                fullWidth
                error={this.state.errors.glider}
              />
              )}
            />
          </td>
          <td>
            <Autocomplete
              autoHighlight
              PopperComponent={PopupPopper}
              disableClearable
              freeSolo
              options={tips.tasks.filter(el => el.toLowerCase().startsWith(this.state.inputs.task.toLowerCase()))}
              value={this.state.inputs.task}
              onChange={this.inputChange('task')}
              onInputChange={this.inputChange('task')}
              renderInput={params => (
                <TextField
                  {...params}
                  multiline
                  fullWidth
                  error={this.state.errors.task}
                />
              )}
            />
          </td>
          <td >
            <Autocomplete
              autoHighlight
              PopperComponent={PopupPopper}
              disableClearable
              freeSolo
              options={tips.startTypeOptions.filter(el => el.toLowerCase().startsWith(this.state.inputs.startType.toLowerCase()))}
              value={this.state.inputs.startType}
              onChange={this.inputChange('startType')}
              onInputChange={this.inputChange('startType')}
              renderInput={params => (
                <TextField
                  {...params}
                  multiline
                  fullWidth
                  error={this.state.errors.startType}
                />
              )}
            />
            {this.state.inputs.startType === 'samolot' &&
              <TimePicker
                KeyboardButtonProps={{ size: 'small' }}
                keyboardIcon={<ClockIcon />}
                ampm={false}
                style={{ width: 110 }}
                invalidDateMessage='Zły format'
                error={this.state.isTimeError}
                openTo='hours'
                views={['hours', 'minutes', 'seconds']}
                format='HH:mm:ss'
                value={this.state.towTime || this.props._date}
                onChange={this.setTime('towTime')}
              />

            }
            {(this.state.inputs.startType === 'wyciągarka' || this.state.inputs.startType === 'autohol') &&
              <Autocomplete
                autoHighlight
                PopperComponent={PopupPopper}
                disableClearable
                freeSolo
                options={tips.people.filter(el => el.toLowerCase().startsWith(this.state.inputs.operator.toLowerCase()))}
                value={this.state.inputs.operator}
                onChange={this.inputChange('operator')}
                onInputChange={this.inputChange('operator')}
                renderInput={params => (
                  <TextField
                    {...params}
                    multiline
                    fullWidth
                    placeholder='operator'
                    error={this.state.errors.operator}
                  />
                )}
              />
            }
          </td>
          <td>
            {flightHours}:{flightMinutes}
          </td>
        </MuiPickersUtilsProvider>
        <td >
          <IconButton
            onClick={this.save}
          >
            <DoneIcon
              color={(this.state.isError || this.state.isTimeError) ? 'error' : 'primary'}
            />
          </IconButton>
          <IconButton
            onClick={() => this.setState({ modalDeleteConfirmation: true })}
          >
            <DeleteIcon
              color='primary'
            />
          </IconButton>
          <Modal
            isOpen={this.state.modalDeleteConfirmation}
            toggle={() => this.setState({ modalDeleteConfirmation: false })}
            centered
          >
            <ModalBody className='chronometer__modal__body'>
              Czy na pewno chcesz usunąć lot {this.state.id}? Lot zostanie trwale usunięty.
            </ModalBody>
            <ModalFooter>
              <Button color='primary' onClick={this.deleteItem} > Tak, usuń</Button>
              <Button color='secondary' onClick={() => this.setState({ modalDeleteConfirmation: false })}> Nie, wróć</Button>
            </ModalFooter>
          </Modal>
        </td>
      </tr>
    )
  }
}

export default EditItem