import React from 'react'
import { connect } from 'react-redux'

import addLeadingZero from '../../utils/addLeadingZero'

class TowTime extends React.Component {
  componentDidMount() {
    this.interval = setInterval(this.tick, 1000)
  }

  componentWillUnmount() {
    clearInterval(this.interval)
  }

  tick = () => this.forceUpdate()

  render() {
    const now = new Date().getTime()
    let towRawSeconds = (now - this.props._different - this.props.start) / 1000
    if (towRawSeconds < 0) towRawSeconds = 0
    const towMinutes = (Math.floor(towRawSeconds / 60))
    const towSeconds = addLeadingZero(Math.round(towRawSeconds % 60))

    return (
      <React.Fragment>
        {towMinutes}'{towSeconds}''
      </React.Fragment>
    )
  }
}


const mapStateToProps = state => ({
  _different: state.clock.different
})


export default connect(
  mapStateToProps,
)(TowTime)