import addLeadingZero from '../utils/addLeadingZero'

const setDifferent = 'clock/SET_DIFFERENT'
const addError = 'clock/ADD_ERROR'
const clockUpdate = 'clock/UPDATE'
const forceUpdate = 'clock/FORCE_UPDATE'

export const getDateFromApi = () => (dispatch, getState) => {
  const errors = getState().clock.errors

  fetch('http://worldtimeapi.org/api/timezone/Europe/Warsaw')
    .then(r => r.json())
    .then(data => {
      const dateFromApi = new Date(data.datetime.slice(0, -6))
      const apiTimeStamp = dateFromApi.getTime()

      const dateFromPc = new Date()
      const pcTimeStamp = dateFromPc.getTime()

      const different = pcTimeStamp - apiTimeStamp

      dispatch(setDifferentAction(different))
    })
    .catch(() => {
      if (errors < 5) {
        dispatch(addErrorAction())
        dispatch(getDateFromApi())
      }
    })
}

export const forceUpdateClockActionCreator = () => ({ type: forceUpdate })

export const clockUpdateAction = (time, date, timeStamp) => {
  const day = date.getDate()
  const month = date.getMonth() + 1
  const year = date.getFullYear()
  const today = { day, month, year }

  return {
    type: clockUpdate,
    today,
    time,
    date,
    timeStamp
  }
}

const addErrorAction = () => ({ type: addError })

const setDifferentAction = (different) => ({
  type: setDifferent,
  different
})

const date = new Date()
const h = addLeadingZero(date.getHours())
const m = addLeadingZero(date.getMinutes())
const s = addLeadingZero(date.getSeconds())
const time = { h, m, s }

const day = date.getDate()
const month = date.getMonth() + 1
const year = date.getFullYear()
const today = { day, month, year }

const initialState = {
  different: 0,
  errors: 0,
  forceUpdate: false,
  date,
  timeStamp: date.getTime(),
  time,
  today
}

export default (state = initialState, action) => {
  switch (action.type) {
    case setDifferent:
      return {
        ...state,
        different: action.different
      }
    case addError:
      return {
        ...state,
        errors: state.errors + 1
      }
    case clockUpdate:
      return {
        ...state,
        today: action.today,
        time: action.time,
        date: action.date,
        timeStamp: action.timeStamp,
        forceUpdate: false
      }
    case forceUpdate:
      return {
        ...state,
        forceUpdate: true
      }
    default:
      return state
  }
}