export const addFlight = 'chronometer/ADD_FLIGHT'
export const startEditing = 'chronometer/START_EDITING'
export const editFlight = 'chronometer/EDIT_FLIGHT'
export const deleteFlight = 'chronometer/DELETE_FLIGHT'
export const endFlight = 'chronometer/END_FLIGHT'
export const endTow = 'chronometer/END_TOW'

export const addFlightActionCreator = flight => ({ type: addFlight, flight })

export const editFlightActionCreator = flight => ({ type: editFlight, flight })

export const deleteFlightActionCreator = id => ({ type: deleteFlight, id })

export const startEditingActionCreator = id => ({ type: startEditing, id })

export const endFlightActionCreator = (id, type, towTime) => (dispatch, getState) => {
  const different = getState().clock.different
  const timeStamp = new Date() - different
  dispatch({ type: endFlight, id, timeStamp })
  if (type === 'samolot' && !towTime) {
    dispatch({ type: endTow, id, timeStamp })
  }
}

export const endTowActionCreator = (id) => (dispatch, getState) => {
  const different = getState().clock.different
  const timeStamp = new Date() - different
  dispatch({ type: endTow, id, timeStamp })
}

export const initialState = {
  allowEdit: true,
  editingFlightId: null,
  nextId: 3,
  data: [{
    id: 1,
    startTime: new Date().getTime() - 30000000,
    endTime: new Date().getTime() - 15000000,
    endTow: new Date().getTime() - 30000000 + 95000,
    pilot: 8,
    instructor: 12,
    glider: 'dwuster',
    task: 'dookoła świata',
    startType: 'wyciągarka',
    operator: '13',
    edit: true
  },
  {
    id: 2,
    startTime: new Date().getTime() - 20000000,
    endTime: new Date().getTime() - 10000000,
    endTow: new Date().getTime() - 20000000 + 145000,
    pilot: 12,
    instructor: 14,
    glider: 'jednoster',
    task: 'dookoła świata',
    startType: 'samolot',
    operator: 'jan',
    edit: true
  },
  ]
}

export default (state = initialState, action) => {
  switch (action.type) {
    case addFlight:
      return {
        ...state,
        nextId: state.nextId + 1,
        data: [
          ...state.data,
          {
            ...action.flight,
            id: state.nextId,
            edit: true
          }
        ]
      }
    case startEditing:
      return {
        ...state,
        allowEdit: false,
        editingFlightId: action.id
      }
    case editFlight:
      return {
        ...state,
        data: state.data.map(el => el.id === action.flight.id ? action.flight : el),
        allowEdit: true,
        editingFlightId: null
      }
    case deleteFlight:
      return {
        ...state,
        data: state.data.filter(el => el.id !== action.id),
        allowEdit: true,
        editingFlightId: null
      }
    case endFlight:
      return {
        ...state,
        data: state.data.map(el => el.id === action.id ? { ...el, endTime: action.timeStamp } : el)
      }
    case endTow:
      return {
        ...state,
        data: state.data.map(el => el.id === action.id ? { ...el, endTow: action.timeStamp } : el)
      }
    default:
      return state
  }
}