import React, { Component } from 'react'

import { connect } from 'react-redux'
import { endFlightActionCreator, endTowActionCreator, deleteFlightActionCreator, editFlightActionCreator } from '../../state/chronometer'

import { Row, Col, Card, CardHeader, CardBody } from 'reactstrap'

import Table from '../../components/Table'
import rawColumns from './columns'
import AddItem from './AddItem'
import EditItem from './EditItem'
import EditButton from './EditButton'
import EndFlightButton from './EndFlightButton'
import FlightTime from './FlightTime'
import TowTime from './TowTime'

import { Switch, FormControlLabel, FormGroup } from '@material-ui/core'
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';


import addLeadingZero from '../../utils/addLeadingZero'

import './Chronometer.css'
import '../../components/Table/Table.css'



class Chronometer extends Component {
  state = {
    sortDirection: 1,
    sortBy: 'date',
    editMode: false,
    columns: [],
    data: [],
    dataToShow: [],
    editingFlight: false,
    editingFlightId: null
  }


  componentDidMount() {
    const columns = this.prepareColumns()
    const mappedData = this.mapData(this.props._data)
    const sortedMappedData = this.sortData(mappedData)
    const dataToShow = this.prepareData(sortedMappedData)

    this.setState({ columns, data: sortedMappedData, dataToShow })
  }

  loading = () => <div className="animated fadeIn pt-1 text-center">Loading...</div>

  componentDidUpdate(prevProps) {
    if (prevProps._data !== this.props._data) {
      const mappedData = (this.mapData(this.props._data))
      const sortedMappedData = this.sortData(mappedData)
      const dataToShow = this.prepareData(sortedMappedData)
      this.setState({ data: sortedMappedData, dataToShow })
    }
  }

  startEditingFlight = id => {
    const dataToShow = this.prepareData(this.state.data, undefined, true, id)

    this.setState({ editingFlight: true, dataToShow })
  }

  toggleEditMode = () => {
    const editMode = !this.state.editMode
    const newColumns = this.prepareColumns(undefined, undefined, editMode)
    const dataToShow = this.prepareData(this.state.data, editMode, false)
    this.setState({ editMode, columns: newColumns, dataToShow, editingFlight: false })
  }

  mapData(data) {
    return data.map(el => {
      const flightStartDate = new Date(el.startTime)
      const flightEndDate = new Date(el.endTime)
      const today = {
        day: addLeadingZero(flightStartDate.getDate()),
        month: addLeadingZero(flightStartDate.getMonth() + 1),
        year: flightStartDate.getFullYear() - 2000
      }

      const flightStartTime = {
        seconds: addLeadingZero(flightStartDate.getSeconds()),
        minutes: addLeadingZero(flightStartDate.getMinutes()),
        hours: addLeadingZero(flightStartDate.getHours())
      }

      const mappedData = {
        id: el.id,
        date: `${today.day}/${today.month}/${today.year}`,
        startTime: `${flightStartTime.hours}:${flightStartTime.minutes}:${flightStartTime.seconds}`,
        pilot: el.pilot,
        instructor: el.instructor,
        glider: el.glider,
        task: el.task,
        startType: el.startType,
      }

      if (el.endTime) {
        const flightEndTime = {
          seconds: addLeadingZero(flightEndDate.getSeconds()),
          minutes: addLeadingZero(flightEndDate.getMinutes()),
          hours: addLeadingZero(flightEndDate.getHours())
        }

        const flightRawSeconds = (el.endTime - el.startTime) / 1000
        const flightHours = addLeadingZero(Math.floor(flightRawSeconds / 3600))
        const flightMinutes = addLeadingZero(Math.round(flightRawSeconds % 3600 / 60))

        Object.assign(mappedData, {
          endTime: `${flightEndTime.hours}:${flightEndTime.minutes}:${flightEndTime.seconds}`,
          flightTime: `${flightHours}:${flightMinutes}`
        })
      } else {
        Object.assign(mappedData, {
          endTimeProps: { style: { textAlign: 'center' } },
          endTime: <EndFlightButton onClick={() => this.props._endFlight(el.id, el.startType, el.endTow)} />,
          flightTime: <FlightTime start={el.startTime} />
        })
      }

      if (mappedData.startType === 'samolot') {
        Object.assign(mappedData, {
          startTypeProps: { rowSpan: 1 },
        })
        if (el.endTow) {
          const endTowDate = new Date(el.endTow)
          const endTowTime = {
            seconds: addLeadingZero(endTowDate.getSeconds()),
            minutes: addLeadingZero(endTowDate.getMinutes()),
            hours: addLeadingZero(endTowDate.getHours())
          }
          const towRawSeconds = (el.endTow - el.startTime) / 1000
          const towMinutes = (Math.floor(towRawSeconds / 60))
          const towSeconds = addLeadingZero(Math.round(towRawSeconds % 60))
          Object.assign(mappedData, {
            startTypeSub: `${endTowTime.hours}:${endTowTime.minutes}:${endTowTime.seconds} - ${towMinutes}'${towSeconds}''`
          })
        } else {
          Object.assign(mappedData, {
            startTypeSub: <div style={{ display: 'flex', justifyContent: 'space-around' }}>
              <EndFlightButton onClick={() => this.props._endTow(el.id)} small />
              <TowTime start={el.startTime} />
            </div>
          })
        }
      }
      if (mappedData.startType === 'wyciągarka' || mappedData.startType === 'autohol') {
        Object.assign(mappedData, {
          startTypeProps: { rowSpan: 1 },
          startTypeSub: el.operator
        })
      }

      return mappedData
    })
  }

  prepareData(data, editMode = this.state.editMode, editingFlight = this.state.editingFlight, editingFlightId) {
    return data.reduce((red, el, i) => {
      const data = this.props._data.find(el => el.id === editingFlightId)
      if (editingFlightId === el.id) {
        return [
          ...red,
          {
            row: <EditItem key='edit-item' data={data} deleteFlight={this.deleteFlight} editFlight={this.editFlight} />
          }
        ]
      }

      return [...red,
      {
        ...el,
        edit: (editingFlight ?
          null
          : typeof el.endTime === 'string' ?
            <EditButton onClick={() => this.startEditingFlight(el.id)} />
            :
            null
        )
      },
      {
        row: <tr key={'bonus-row-' + i}>
          {el.startTypeSub &&
            <td style={{ whiteSpace: 'nowrap' }}>{el.startTypeSub}</td>
          }
        </tr>
      }]
    }, [{ row: <AddItem key='add-item' editMode={editMode} editingFlight={editingFlight} /> }])
  }

  sortData(data, sortBy = this.state.sortBy, sortDirection = this.state.sortDirection) {
    if (sortBy === 'date') sortBy = 'startTime'
    const sortedRawData = [...this.props._data].sort((a, b) => {
      let first = a[sortBy]
      let second = b[sortBy]

      if (sortBy === 'flightTime') {
        first = (a.endTime || new Date().getTime()) - a.startTime
        second = (b.endTime || new Date().getTime()) - b.startTime
      }
      if (first < second) {
        return sortDirection
      }
      if (first > second) {
        return -1 * sortDirection
      }
      return 0
    })

    return sortedRawData.map(rawEl => data.find(mappedEl => rawEl.id === mappedEl.id))
  }

  changeSortBy = sortBy => {
    const sortDirection = (sortBy === this.state.sortBy ? -1 * this.state.sortDirection : 1)
    const newColumns = this.prepareColumns(sortBy, sortDirection)
    const sortedMappedData = this.sortData(this.state.data, sortBy, sortDirection)
    const dataToShow = this.prepareData(sortedMappedData)
    this.setState({ data: sortedMappedData, dataToShow, columns: newColumns, sortBy, sortDirection })
  }

  prepareColumns = (sortBy = this.state.sortBy, sortDirection = this.state.sortDirection, editMode = this.state.editMode) => {
    return rawColumns.reduce((red, el) => {
      if (!editMode && el.key === 'edit') return red
      const column = {
        ...el,
        props: { onClick: () => this.changeSortBy(el.key) }
      }
      if (el.key === sortBy) {
        column.title = <span style={{ display: 'flex', alignItems: 'center' }}>{el.title} {sortDirection === 1 ? <ArrowDropDownIcon /> : <ArrowDropUpIcon />}</span>
      }
      return [...red, column]
    }, [])
  }

  deleteFlight = id => {
    this.setState({ editingFlight: false }, () => {
      this.props._deleteFlight(id)
    })
  }

  editFlight = flight => {
    this.setState({ editingFlight: false }, () => {
      this.props._editFlight(flight)
    })
  }

  render() {
    return (
      <div>
        <Row>
          <Col md="12" sm="12" xs="12">
            <Card>
              <CardHeader className='chronometer__header'>
                Tabela lotów
                <FormGroup row className='chronometer__edit-mode--switch'>
                  <FormControlLabel
                    style={{ margin: 0 }}
                    control={
                      <Switch
                        checked={this.state.editMode}
                        onChange={this.toggleEditMode}
                        color='primary'
                        edge='start'
                        disabled={this.state.editingFlight}
                      />
                    }
                    label='Tryb edycji'
                  >
                  </FormControlLabel>
                </FormGroup>
              </CardHeader>
              <CardBody>
                <div className='chronometer__table'>
                  <Table
                    columns={this.state.columns}
                    data={this.state.dataToShow}
                    td={{ rowSpan: 2 }}
                    th={{
                      style: {
                        cursor: 'pointer',
                      }
                    }}
                  />
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  _data: state.chronometer.data,
})

const mapDispatchToProps = dispatch => ({
  _endFlight: (...args) => dispatch(endFlightActionCreator(...args)),
  _endTow: (...args) => dispatch(endTowActionCreator(...args)),
  _deleteFlight: (...args) => dispatch(deleteFlightActionCreator(...args)),
  _editFlight: (...args) => dispatch(editFlightActionCreator(...args))
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Chronometer)
